.movie {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    height: fit-content;
    margin: 8px 16px;
    border: #F7F7F7 solid 5px;
    text-align: left;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: all 200ms ease;
}

.movie__poster {
    width: 224px;
    height: 336px;
    cursor: pointer;
    object-fit: cover;
}


.movie:hover {
    border-color: var(--main-color);
}

.movie__description {
    text-align: left;
    width: 216px;
    margin: 4px;
}

.movie__title {
    font-size: 14px;
    margin-bottom: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left;
}

.movie__title:hover {
    cursor: pointer;
}

.movie__type {
    display: flex;
    font-size: 13px;
    color: #b6b6b6;
}

.movie__info--wrapper {
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
}

.movie__year {
    color: #b6b6b6;
}

.movie__link {
    font-weight: 900;
    color: var(--main-color);
    text-decoration: none;
    margin-left: 5px;
    font-size: 14px;
}

.movie__items {
    display: flex;
    align-self: center;
    justify-content: center;
}

.movie__info--wrapper>.MuiSvgIcon-root {
    margin-left: auto;
    cursor: pointer;
    font-size: 19px;
}

/* SKELETON */

.movie__poster--skeleton {
    width: 224px;
    height: 336px;
    background-color: lightgrey;
}

.movie__description--skeleton {
    width: 216px;
    height: 48px;
    margin: 4px;
}

.movie__title--skeleton {
    width: 216px;
    height: 16px;
    margin-bottom: 12px;
    background-color: lightgrey;
}

.movie__type--skeleton {
    width: 34px;
    height: 10px;
    margin-right: 5px;
    background-color: lightgrey;
}

.movie__info--wrapper--skeleton {
    width: 100%;
    height: 20px;
    background-color: lightgrey;
}